<template>
  <div>
    <div id="app">
      <div id="bdy" ref="bdy"></div>
      <v-app>
        <v-main>
          <v-navigation-drawer
            v-model="menu"
            dark
            absolute
            left
            temporary
            fixed
            clipped
            style="position: fixed; top: 56px"
          >
            <v-list nav dense>
              <v-list-item-group active-class="cyan darken-1">
                <v-list-item v-for="(item, i) in list" :key="i" link>
                  <v-list-item-title @click="git(item)">
                    {{ item.label }}</v-list-item-title
                  >
                </v-list-item>
              </v-list-item-group>
            </v-list>
          </v-navigation-drawer>
          <!-- <v-app-bar dark app  color="cyan darken-1" height="56"   src="img/turkuaz.png" > -->
          <v-app-bar dark app color="cyan darken-1" height="56">
            <v-app-bar-nav-icon @click.stop="geri()"> </v-app-bar-nav-icon>

            <v-img id="logo" @click="geri()" src="img/logo-light.svg"></v-img>
          </v-app-bar>

          <v-slide-y-transition mode="out-in">
            <router-view />
          </v-slide-y-transition>
        </v-main>
      </v-app>
    </div>
  </div>
</template>
<script>
export default {
  data: () => ({
    menu: false,
    list: [
      { label: "Ana Men", page: "menu", path: "/" },
      { label: "Randevu Al", page: "mudurluksec", path: "/mudurluksec" },
      { label: "Randevularm", page: "randevularim", path: "/randevularim" },
      { label: "Bavurularm", page: "basvurularim", path: "/basvurularim" },
      {
        label: "Pasaport Talep Formlar",
        page: "pasaporttalepformlari",
        path: "/pasaporttalepformlari",
      },
      {
        label: "Bavuru cretleri",
        page: "basvuruucretleri",
        path: "/basvuruucretleri",
      },
      {
        label: "Ska Sorulan Sorular",
        page: "sikcasorulansorular",
        path: "/sikcasorulansorular",
      },
      {
        label: "QR",
        page: "qrmenu",
        path: "/qrmenu",
      },
    ],
  }),
  methods: {
    geri() {
      // if (this.$router.history.current.path!='/') {
      //   this.$router.push({ path: '/' });
      // }
      this.menu = !this.menu;
    },
    git(item) {
      this.menu = false;
      if (this.$router.history.current.path != item.path) {
        this.$router.push({
          path: item.path,
        });
      }
    },
    reloadAppForNewRelease(e) {
      this.registration = e.detail;
      this.updateExists = true;
      this.updateExists = false;
      if (!this.registration || !this.registration.waiting) {
        return;
      }
      this.registration.waiting.postMessage("skipWaiting");
    },
  },
  mounted() {
    this.$refs.bdy.style.backgroundImage = "url('"+process.env.VUE_APP_BASEURL+"img/logo.svg')";
  },
  created() {
    document.addEventListener("swUpdated", this.reloadAppForNewRelease, {
      once: true,
    });

    if (navigator.serviceWorker) {
      navigator.serviceWorker.addEventListener("controllerchange", () => {
        if (this.refreshing) return;
        this.refreshing = true;

        setTimeout(() => {
          window.location.reload();
        }, 2000);
      });
    }
  },
};
</script>
